@import "./global.scss";

.app {
  height: 100vh;
  // background: url(./assets/space.jpeg) repeat;

  .sections {
    width: 100%;
    height: calc(100vh - 70px);
    color: white;
    position: relative;
    top: 70px;
    //has it so when user navigates through topbar link, it has a smooth transition
    scroll-behavior: smooth;
    //makes sure it lands and snaps to each component
    // scroll-snap-type: y mandatory;
    scrollbar-width: none; //for firedox
    &::-webkit-scrollbar {
      display: none; //for all other browsers
    }
    // > * {
    //   width: 100vw;
    //   height: calc(100vh - 70px);
    //   scroll-snap-align: start;
    // }
    // @include mobile {
    //   scroll-snap-type: none;
    // }
  }
}
